.Form{background:#fff;display:flex;flex-direction:column;padding:22px 40px;border-radius:10px;box-shadow:-1px 2px 5px #7e7e7e;width:450px;margin:0 20px;height:540px}.Form *:not(h2){margin-top:16px !important;width:100%}.Form input{height:40px;border-radius:6px;border-width:1px;padding:10px}.Form textarea{height:140px;border-radius:6px;border-width:1px;padding:10px}.Form textarea+.text-length-indicator{font-size:small;color:#3a3a3a;text-align:right;margin-top:10px !important}.Form .actions{padding:0 4px}.Form .actions button{width:unset;background:none;border:none;font-size:medium;margin:0 !important;border-radius:20px;cursor:pointer}.Form .actions button[type=submit]{background:#000;color:#fff;padding:8px 16px;width:100px}.Form .actions button[type=submit] i{display:none;margin:0 !important}.Form .actions button[type=submit] i::after{content:"↷";display:flex;align-items:center;justify-content:center;height:10px;margin:0 !important;animation:spin infinite 2s linear}.Form .actions button[type=submit].pending-dub i{display:block}.Form .actions button[type=button]{text-decoration:underline}.Form.pending-sub{opacity:.5;pointer-events:none}.Form.pending-sub i{display:block !important;margin:0 0 0 6px !important}