.message-list{list-style:none;margin-top:11px;padding:11px 22px 22px 0px;overflow:auto}.message-list li{position:relative;padding:6px;border:1px solid;border-radius:6px;margin-top:32px;margin-bottom:64px;min-height:40px}.message-list li .message-li-phone{position:absolute;bottom:calc(100% + 16px);font-size:small;left:0}.message-list li .message-li-phone::after{content:"";display:none;position:absolute;left:100%;top:0;font-size:18px;height:18px;align-items:center;justify-content:center;z-index:1000}.message-list li .message-li-date{position:absolute;bottom:calc(100% + 16px);white-space:nowrap;font-size:small;right:0}.message-list li .message-li-length{position:absolute;top:100%;right:0;font-size:small;color:#3a3a3a}.message-list li.status-failed .message-li-phone::after{content:"✖";display:flex}.message-list li.status-queued .message-li-phone::after{content:"✔";display:flex}.message-list li.status-sent .message-li-phone::after{content:"✔✔";display:flex}